export default {
  PROJECT_RECEIVE(state, project) {
    state.project = project;
  },

  WORK_RECEIVE(state, work) {
    state.work = work;
  },

  EXPERIENCE_RECEIVE(state, experience) {
    state.experience = experience;
  },

  SKILL_RECEIVE(state, skill) {
    state.skill = skill;
  },
};



